export const Requirements = () => {
    return (
        <>
        <div style={displayBlock}>
             <div style={modalbox}>
             <div style={textBox}>
             <img style={imgD} src={img} alt="success"/>
             <p style={titleEelm}>{props.title}</p>
             {props.text}
             </div>
             <button style={closeOne}  onClick={() => props.setShow(false)}>
                 X
             </button>
           </div>
       </div>
        </>
     )
}